@import '@';

$navButtonBasis: 2%;

.ImageSlider {
  overflow: hidden;
}

.main {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}

.imageContainer {
  position: relative;
  @include o-square(100%);
  -webkit-flex-basis: 100 - ($navButtonBasis * 2);
      -ms-flex-preferred-size: 100 - ($navButtonBasis * 2);
          flex-basis: 100 - ($navButtonBasis * 2);
  padding: $bump;
  overflow: hidden;
  min-height: 360px;
  max-height: 1024px;
}

.navButton {
  -webkit-flex-basis: $navButtonBasis;
      -ms-flex-preferred-size: $navButtonBasis;
          flex-basis: $navButtonBasis;
  border: none;
  background: none;
  cursor: pointer;
}

.arrow {
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
  &.disabled {
    opacity: 0;
  }
}

.arrowLeft {
  @include arrow(left, $unit, $arrow-color);
}
.arrowRight {
  @include arrow(right, $unit, $arrow-color);
}

.image {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: auto;
  max-width: 98%;
  max-height: 98%;
  margin: auto;
  box-shadow: $box-shadow;
  &.active {
    display: block;
  }
}

.meta {
  text-align: center;
}

.thumbs {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: $bump;
}

.thumb {
  @include o-size(64px);
  margin: $bump/2;
  background-size: 128px;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: $box-shadow;
  cursor: pointer;
  &.active {
    box-shadow: none;
    border: 1px solid o-md-color('pink', 400);
  }
}



