@import '@';

.Header {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-around;
      -ms-flex-pack: distribute;
          justify-content: space-around;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  padding: $unit;
  border-bottom: $dashed-border;
}

.bannerContainer {
  -webkit-box-flex: 3;
  -webkit-flex: 3;
      -ms-flex: 3;
          flex: 3;
  text-align: center;
}

.banner {
  font-size: 4em;
  margin: 0;
}

.banner:hover,
.navLink a:hover {
  -webkit-transition: letter-spacing 0.33s ease-in-out;
  transition: letter-spacing 0.33s ease-in-out;
  &:hover {
    letter-spacing: 4px;
  }
}

.captionText {
  font-size: $h3-font-size;
  margin: 0;
}

.navLink {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.dummy {
  opacity: 0;
}



