@import '@';

/* see Home for Issue style */

.issueInner {
  @extend %pageInner;
}

.overlay {
  @extend %overlay;
}

.thumbs {
  @include o-list-unstyled;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  li {
    -webkit-flex-basis: 25%;
        -ms-flex-preferred-size: 25%;
            flex-basis: 25%;
    min-width: 256px;
    margin: $bump;
  }
}

.title {
  text-align: center;
}



